import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Signup from '../views/Signup.vue'
import Signup_before from '../views/Signup_before.vue'
import Farm_Register from '../views/Farm_Register.vue'
import Board_list from '../views/Board_list.vue'
import Item_buy from '../views/Item_buy.vue'
import Item_bucket from '../views/Item_bucket.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/Signup',
    name: 'Signup',
    component: Signup
  },
  {
    path: '/Signup_before',
    name: 'Signup_before',
    component: Signup_before
  },
  {
    path: '/Farm_Register',
    name: 'Farm_Register',
    component: Farm_Register
  },
  {
    path: '/Board_list',
    name: 'Board_list',
    component: Board_list
  },
  {
    path: '/Item_buy',
    name: 'Item_buy',
    component: Item_buy
  },
  {
    path: '/Item_bucket',
    name: 'Item_bucket',
    component: Item_bucket
  }
]

const router = new VueRouter({
  routes
})

export default router
