<template>
  <div class="signup">
      <div class="section section--signup">
        <div class="inner">
          <div class="inner-border">
            <div class="inner--signup">
              <img class="logo" src="../assets/images/logo.png" alt="">
              <p class="signup__title">원하는 농장과 농작물들을 보려면 가입하세요</p>
              <a class="btn btn-primary mb-3" href="#">구글계정으로 로그인</a>
              <div class="line">
                <p class="or">또는</p>
              </div>
              <form @submit.prevent="onSubmit" 
                    action="http://localhost:8888/farmpam/users/vue/signup.do"
                    method="post"
                    id="myForm"
                    ref="signupForm" 
                    class="signup--form row g-3">

                <div class="col-12 input__text">
                  <input type="text" name="users_email" id="users_email" 
                  :class="{
                    'is-invalid': !isEmailValid,
                    'is-valid': isEmailValid,
                  }"
                  v-model="inputEmail"
                  placeholder="이메일 주소" />

                  <div class="invalid-feedback">사용할 수 없는 닉네임입니다.</div>
                </div>
                
                <div class="col-12 form-group input__text">
                  <input type="password" name="users_pwd" id="users_pwd" 
                  placeholder="비밀번호"
                  required
                  v-model="users_pwd">
                  <small class="form-text text-muted">
                    5글자~10글자 이내로 입력하세요.</small>
                  <div class="invalid-feedback">비밀번호를 확인하세요</div>
                </div>

                <div class="col-12 form-group input__text">
                  <input type="password" name="users_pwd2" id="users_pwd2" 
                  placeholder="비밀번호 확인"
                  required
                  v-model="users_pwd2" />
                </div>

                <div class="col-12 input__text">
                  <input type="text" name="users_name" id="users_name" 
                  placeholder="성명" required>
                </div>

                <div class="col-12 input__text">
                  <input type="text" name="users_addr" id="users_addr" 
                  placeholder="주소" required>  

                  <button id="addr_search"
                    class="btn btn-primary"
                    type="button"
                    @click="mapBtn">
                    검색하기
                    </button>
                </div>

                <div class="col-12 input__text">
                  <input type="text" name="users_addr_detail" id="users_addr_detail" 
                  placeholder="상세주소" required>
                </div>

                <div class="col-12 mb-5 input__text">
                  <input type="text" name="users_phone" id="users_phone" 
                  placeholder="핸드폰번호 입력" maxlength="13" required>
                </div>

                <div class="col-12">
                  <button class="btn btn-success" type="submit"> 회원가입</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import route from "../router";

export default {
  name: "Signup",
  data() {
    return {
      isEmailValid: false,
      inputEmail: "",
      users_pwd: "",
      users_pwd2: ""
    };
  },
  computed: {},
  watch: {
    inputEmail: function (newEmail) {
      this.$http
        .get(
          "http://localhost:8888/farmpam/users/checkusersemail.do?inputUsersEmail=" +
            newEmail
        )
        .then((res) => {
          //data 는 {isExist:true} or {isExist:false} 형태의 object 이다.
          if (res.data.isExist) {
            //만일 존재한다면
            //사용할수 없는 아이디라는 피드백을 보이게 한다.
            this.isEmailValid = false;
          } else {
            this.isEmailValid = true;
          }
        });
    },
  },
  methods: {
    mapBtn() {
      //카카오 지도 발생
      new window.daum.Postcode({
        oncomplete: function (data) {
          //선택시 입력값 세팅
          // eslint-disable-next-line no-unused-vars
          let users_addr = (document.getElementById("users_addr").value =
            data.address); // 주소 넣기
          document.querySelector("input[name=users_addr_detail]").focus(); //상세입력 포커싱
        },
      }).open();
    },
    onSubmit() {
      let check =
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,16}$/;
      let check2 =
        /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      //새 비밀번호와 비밀번호 확인이 일치하지 않으면 폼 전송을 막는다.
      if (this.users_pwd != this.users_pwd2) {
        alert("비밀번호가 일치하지 않습니다.");
        return false;
      } else if (!check.test(this.users_pwd)) {
        alert(
          "비밀번호는 숫자, 영문, 특수문자를 포함하여 8글자 이상 16글자 이하로 작성해주세요."
        );
        return false;
      } else if (!check2.test(this.inputEmail)) {
        alert("이메일 형식에 맞게 작성해주세요.");
        return false;
      } else if (this.isEmailValid != true) {
        alert("존재하는 이메일입니다. 다시 확인해 주세요.");
      } else {
        const formData = new FormData(this.$refs.signupForm);
        console.log("axiostest");
        this.$http
          .post("http://localhost:8888/farmpam/users/signup.do", formData)
          .then((res) => {
            console.log(res);
            alert("회원가입 성공!");
            route.push({ name: "Home" });
          });
      }
    },
  },
};
</script>

<style scoped>

.signup {
    background-color: rgba(var(--b3f,250,250,250),1); 
}

.signup__title {
  margin-bottom: 20px;
}

.inner-border{
  width: 564px;
  height: 800px;
  border: 1px solid rgba(var(--b6a, 219, 219, 219),1);
  padding-top: 40px;
	padding-bottom: 40px;
  display: flex;
  background-color: #ffffff; 
}

.inner--signup {
  margin: auto; 
  text-align: center;
  
}

input{
  width: 250px;
}

.logo {
    width: 300px;
    height: 140px;
}

.btn {
  width: 250px;
}

.line--title {
  margin-bottom: 20px;
}

.line {
  display:flex;
  flex-basis: 100%;
  align-items: center;
  color:rgba(0, 0, 0, 0.35);
  font-size: 14px;
  margin: 20px 100px;
}

.line::before {
  content: "";
  flex-grow: 1;
  margin: 0px 16px;
  background: rgba(0,0,0,0.35);
  height: 1px;
  font-size: 0px;
  line-height: 0px;
}

.line::after {
  content:"";
  flex-grow: 1;
  margin: 0px 16px;
  background: rgba(0,0,0,0.35);
  height: 1px;
  font-size: 0px;
  line-height: 0px;
}
</style>
